* 
{
  padding: 0;
  margin: 0;
  box-sizing:border-box;
}

body {
  background: #fff;
  color: #444;
  font-family: "Open Sans", sans-serif;
}

a {
  color: #007bff;
  text-decoration: none;
  transition: 0.5s;
}

a:hover, a:active, a:focus {
  color: #0b6bd3;
  outline: none;
  text-decoration: none;
}

p {
  padding: 0;
  margin: 0 0 30px 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  margin: 0 0 20px 0;
  padding: 0;
}
/* header style  */
.mobile-nav-toggle 
{
  color: #000;
  font-size: 28px;
  cursor: pointer;
}
.navbar .lst 
{
  display: none;
}
.navbar.navbar-mobile .lst 
{
  display: block;
}
.dropdown-lst li a 
{
  color: inherit;
}
.navbar .container ul .dropdown-main:hover .dropdown-lst
{
  top: 140%;
  visibility: visible;
  opacity: 1;
}
.dropdown-lst li 
{
  position: relative;
  width: 100%;
  text-align: center;
}
.dropdown-lst 
{
  position: absolute;
  border-radius: 5px;
  z-index: 6;
  background: #fff;
  border: 1px solid #007bff33;
  /* top: 140%; */
  top: 200%;
  width: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: translateX(-40px);
  transition: .4s ease-in-out;
  visibility: hidden;
  opacity: 0;
}
.navbar .container ul li.active,
.navbar .container ul li:hover
{
  color: #258dfc;
}
.navbar .container ul li i 
{
  margin-left: 6px;
}
.navbar .container ul li 
{
  margin: 10px;
  cursor: pointer;
  color: #004894;
  transition: .3s ease-in-out;
  font-weight: 400;
}
.navbar .container ul 
{
  display: flex;
  justify-content:center;
  align-items: center;
  flex-direction: row;
  list-style: none;
  transform: translateY(6px);
}
.navbar .container 
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.navbar .logo img 
{
  transform: scale(.5) translateX(-140px);
  cursor: pointer;
}
.navbar.active-navbar 
{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 4;
  background: #fff;
}
.navbar.navbar-mobile .container ul 
{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.navbar.navbar-mobile .container
{
  display: flex;
  justify-content: flex-start;
  align-items:flex-start;
  flex-direction: column;
}
.navbar.navbar-mobile .lst 
{
  position: absolute;
  top: 2%;
  right: 2%;
}
.navbar.navbar-mobile 
{
  position: fixed;
  width: 360px;
  height: 400px;
  z-index: 99;
  top: 20%;
  left: 30%;
  border-radius: 10px;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.09);
}
.navbar 
{
  position: relative;
  z-index: 5;
  width: 100%;
  background: #fff;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.09);
}
.hero .hero-info div li:last-child:hover
{
  border-color: #007bff;
  background: #007bff;
}
.hero .hero-info div li:last-child
{
  border: 2px solid #fff;
}
.hero .hero-info div li:first-child:hover
{
  background: inherit;
  border-color: #fff;
}
.hero .hero-info div li:first-child
{
  background-color: #007bff;
  border: 2px solid #007bff;
} 
.hero .hero-info div li 
{
  padding: 12px 32px;
  text-transform: capitalize;
  color: #fff;
  border-radius: 50px;
  transition: .3s ease-in-out;
  list-style: none;
  margin: 8px;
  cursor: pointer;
  font-weight: 600;
}
.hero .hero-info div
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-top: 20px;
}
.hero .hero-info h1:nth-child(2) 
{
  color: #228dff;
  text-decoration: underline;
}
.hero .hero-info h1 
{
  font-weight: 600;
  line-height: 43px;
  font-size: 50px;
  text-align: center;
}
.hero 
{
  color: #fff;
  background: no-repeat center bottom;
  position: relative;
  width: 100%;
  z-index: 2;
  padding: 200px 0px 120px 0px;
}
.header 
{
  min-height: 661px;
  width: 100%;
  position: relative;
}
@media screen and (max-width:991px) {
  .hero 
  {
    padding: 0px 0px 0px 0px;
  }
}
@media screen and (max-width:690px){
  .hero .hero-info h1 
  {
    font-size: 35px;
    line-height: 30px;
  }
}
@media screen and (max-width:1010px){
  .dropdown-lst
  {
    left: 140%;
  }
  .navbar .container ul .dropdown-main:hover .dropdown-lst
  {
    top: 60%;
    left: 140%;
  }
  .navbar .lst
  {
    display: block;
  }
  .navbar .container .lnks
  {
    display: none;
  }
}
@media screen and (max-width:720px){
  .navbar.navbar-mobile 
  {
    left: 15%;
  }
}
@media screen and (max-width:530px){
  .navbar.navbar-mobile
  {
    left: 10%;
  }
}
@media screen and (max-width:460px){
  .navbar.navbar-mobile 
  {
    left: 7%;
  }
}
@media screen and (max-width:418px){
  .navbar.navbar-mobile 
  {
    left: 4%;
  }
}
/* end of the header style */

/* about style */
.section-tittle p 
{
  font-size: 14px;
  width: 60%;
  text-align: center;
}
.section-tittle h1 
{
  font-weight: 500;
  text-transform: capitalize;
}
.section-tittle
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content:center;
  align-items: center;
  flex-direction: column;
}
.about .row1 p
{
  font-size: 16px;
}
.about .row1 ul li i 
{
  font-size: 20px;
  color: #007bff;
}
.about .row1 ul li .icon-box 
{
  border: 2px solid #007bff;
  padding: 15px 20px;
  border-radius:50%;
  margin-right: 20px;
  cursor: pointer;
  transform: translate(-30px, 10px);
  transition: .5s ease-in-out;
}
.about .row1 ul li .text p 
{
  font-size:14px;
  transform: translate(-30px, -14px);
}
.about .row1 ul li .text h3  
{
  font-weight: 500;
  font-size: 21px;
  transform: translateX(-30px);
  color: rgb(0, 25, 58);
}
.about .row1 ul li:hover .icon-box i 
{
  color: #fff;
}
.about .row1 ul li:hover .icon-box
{
  background: #007bff;
} 
.about .row1 ul li 
{
  display:flex;
  justify-content:space-between;
  align-items: flex-start;
  flex-direction: row;
}
.about .row1 ul 
{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  list-style: none;
}
  /* row2 */
.about .row2 h1 
{
  font-size: 22px;
  font-weight: 600;
}
.about .row2 ul li 
{
  transform: translateX(-25px);
}
.about .row2 ul li 
{
  font-size: 16px;
}
.about .row2 ul 
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 35px;
  list-style: none;
}
.about .row2
{
  margin-top: 50px;
}
  /* end row2 */
  /* row3 */

  .about .row3 h1 
  {
    font-size: 22px;
    font-weight: 600;
  }
  .about .row3 ul li 
  {
    transform: translateX(-25px);
  }
  .about .row3 ul li 
  {
    font-size: 16px;
  }
  .about .row3 ul 
  {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 35px;
    list-style: none;
  }
  .about .row3
  {
    margin-top: 50px;
  }
  /* end row3 */
.about 
{
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.09);
  padding-bottom: 50px;
  padding-top: 50px;
}
/* about style  */

/* services style */
.services .row 
{
  justify-content: center;
}
.services .row .box i 
{
  font-size: 60px;
  transform: translateY(-20px);
}
.services .row .box .text p 
{
  margin-left: 10px;
  font-size: 15px;
}
.services .row .box .text h4
{
  margin-left: 10px;
  font-weight: 600;
  font-size: 19px;
  transform: translateY(12px);
  transition: .3s ease-in-out;
}
.services .row .box:hover h4
{
  color: #007bff;
}
.services .row .box:hover::before
{
  border-bottom: 4px solid #0031668e;
  visibility: visible;
  opacity: 1;
}
.services .row .box:hover
{
  transform: translateY(-10px);
}
.services .row .box::before
{
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  opacity: 0;
  visibility: hidden;
  border-radius: 10px;
  transition:.3s ease-in-out;
}
.services .row .box
{
  background: #fff;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: relative;
  margin-top: 35px;
  padding-left: 10px;
  padding-right: 10px;
  box-shadow: 1px 1px 60px #007bff15;
  transition: .3s ease-in-out;
  cursor: pointer;
}
.services 
{
  position: relative;
  width: 100%;
  background: #2d92ff23;
  padding-top: 50px;
  padding-bottom: 50px;
}
/* services style */

/* portfolio style */
.portfolio-flters li.filter-active 
{
  color: #fff;
  background: #007bff;
} 
.portfolio-flters li:hover
{
  background: #007bff;
  color: #fff;
} 
.portfolio-flters li 
{
  margin: 10px;
  background: #007bff2a;
  color: #007bff;
  padding: 6px 16px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  transition: .3s ease-in-out;
}
.portfolio-flters 
{
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  list-style: none;
  margin: 0 auto;
  margin-bottom: 50px;
  margin-top: 20px;
}
.portfolio-item .tools i:hover
{
  background: #258dfc;
} 
.portfolio-item .tools i, 
.portfolio-item .tools a i 
{
  font-size: 20px;
  margin: 5px;
  background: #007bff;
  border-radius: 50px;
  padding: 6px 10px;
  cursor: pointer;
  transition: .3s ease-in-out;
}
.portfolio-item .tools a 
{
  color: #fff;
}
.portfolio-item .tools h4:hover
{
  color: #007bff;
  cursor: pointer;
}
.portfolio-item .tools h4
{
  transition: .3s ease-in-out;
}
.portfolio-item .tools p 
{
  transform: translateY(-8px);
  text-transform: uppercase;
}
.portfolio-item .tools2 
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  transform: translateY(-20px);
}
.portfolio-item .tools::after
{
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  top: 2%;
  left: 2%;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
}
.portfolio-item .tools::before
{
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  right: 2%;
  bottom: 2%;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
}
.portfolio-item .tools
{
  position: absolute;
  color: #fff;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background:linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), #007bff9f;
  transform: scale(.6);
  visibility: hidden;
  opacity: 0;
  transition: .3s ease-in-out;
}
.portfolio .portfolio-item
{
  margin-top: 10px;
}
.portfolio .portfolio-item .portfolio-wrapper,
.portfolio .portfolio-item
{
  position: relative;
}
.portfolio .portfolio-item:hover .tools
{
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
.portfolio 
{
  position: relative;
  width: 100%;
  padding-top: 100px;
  padding-bottom: 100px;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.09);
}
/* portfolio style */

