/* footer-copy-right style */
.footer-copyright .container 
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer-copyright p span
{
  color: #007bff;
  cursor: pointer;
}
.footer-copyright p:first-child
{
  transform: translateY(25px);
} 
.footer-copyright p 
{
  font-size: 14px;
}
.footer-copyright 
{
  position: relative;
